import moment from 'moment';

export function verifyData(data) {
  const { 
    isAdult,
    firstName,
    lastName,
    patronymic,
    birthday,
    documentType,
    passportSeries,
    passportNumber,
    birthSertificateNumber
  } = data;

  const result = {
    errorFlags: {},
    errorText: {},
    isReady: true
  };

  if (!lastName) {
    result.errorFlags.lastName = true;
    if (result.isReady) {
      result.errorText = {
        string1: ' ""   '
      };
    }
    result.isReady = false;
  }

  if (!firstName) {
    result.errorFlags.firstName = true;
    if (result.isReady) {
      result.errorText = {
        string1: ' ""   '
      };
    }
    result.isReady = false;
  }

  if (!patronymic) {
    result.errorFlags.patronymic = true;
    if (result.isReady) {
      result.errorText = {
        string1: ' ""   '
      };
    }
    result.isReady = false;
  }

  if (birthday.length !== 10) {
    result.errorFlags.birthday = true;
    if (result.isReady) {
      result.errorText = {
        string1: '      ',
        string2: ' (  ,   ):',
        string3: '25.05.1975'
      };
    }
    result.isReady = false;
  }

  if (!moment(birthday, 'DD.MM.YYYY').isValid()) {
    result.errorFlags.birthday = true;
    if (result.isReady) {
      result.errorText = {
        string1: ' " "   ',
      };
    }
    result.isReady = false;
  }

  if (!isAdult && documentType === ' ') {
    result.errorFlags.documentType = true;
    if (result.isReady) {
      result.errorText = {
        string1: '      '
      };
    }
    result.isReady = false;    
  } 
  
  if (documentType === ' ') {

    if (passportSeries.length !== 4) {
      result.errorFlags.passportSeries = true;
      if (result.isReady) {
        result.errorText = {
          string1: '      ',
          string2: ':',
          string3: '1234'
        };
      }
      result.isReady = false;
    }

    if (passportNumber.length !== 6) {
      result.errorFlags.passportNumber = true;
      if (result.isReady) {
        result.errorText = {
          string1: '      ',
          string2: ':',
          string3: '123456'
        };
      }
      result.isReady = false;
    }

  } else {

    if (!/^[IVX]{2,4}[-]{2}[0-9]{6}$/.test(birthSertificateNumber)) {
      result.errorFlags.birthSertificateNumber = true;
      if (result.isReady) {
        result.errorText = {
          string1: '        ',
          string2: ':',
          string3: 'VIII  123456'
        };
      }
      result.isReady = false;
    }
  }

  return result;
}
