import { useState, useContext } from 'react';
import './SubscriptionForm.css';
import AppContext from 'AppContext';
import { infoBox, errorBox, httpErrorBox } from 'api/gui';
import { verifyEmail } from 'api/utils';
import { setSubscribe } from 'api/http/setSubscribe';

function SubscriptionForm() { 
  const [email, setEmail] = useState('');
  const { setAnimation, setPopup } = useContext(AppContext);
  
  const emailChange = (evt) => {
    setEmail(evt.target.value);
  }

  const sendEmail = async (evt) => {
    evt.preventDefault();
    const mail = email.trim();

    if (!mail) {
      errorBox(setPopup, [
        '   ',
        '         ""',
      ]);
      setEmail('');
      return;
    }

    if (!verifyEmail(mail)) {
      errorBox(setPopup, [
        '    ',
        '    xxx@xxx.xx',
        ': qwerty111@mail.ru',
      ]);
      setEmail(mail);
      return;      
    }

    setEmail('');
    const response = await setSubscribe(setAnimation, mail);
    if (response.ok) {
      infoBox(setPopup, [
        `   ${mail}  `,
        '           '
      ]);
    } else {
      httpErrorBox(setPopup, response);
    }
  }

  return (
    <form className="subscription-form" onSubmit={sendEmail}>
      <h3 className="subscription-form__title">
        
      </h3>
      <div className="subscription-form__body">
        <label className="subscription-form__label">
             
          <input 
            className="subscription-form__input"
            type="text" 
            name="e-mail" 
            value={email}
            placeholder="e-mail"
            onChange={emailChange}
          />
        </label>
        <button className="subscription-form__button" type="submit">
          
        </button>        
      </div>
    </form>
  )
}

export default SubscriptionForm;